.catalog {
  display: grid;
  grid-template-columns: 200px;
  gap: 48px;
  justify-content: center;
  @include media (488px, 2);
  @include media (768px, 3);
  @include media (1024px, 4);

  &__card {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 300px;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-sizing: border-box;
  }
}
